html, body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  background: #eee;
  font-family: 'Helvetica Neue', arial, sans-serif;
  font-weight: 400;
  color: #444;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

#app {
  height: 100%;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 56px;
  padding: 0;
  background: #000;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  z-index: 50;
}

.header h1 {
  float: left;
  margin: 0;
  padding: 0 15px;
  font-size: 24px;
  line-height: 56px;
  font-weight: 600;
  color: #FFF;
}

.header nav {
  float: right;
  font-size: 100%;
}

.header nav a {
  display: inline-block;
  height: 56px;
  line-height: 56px;
  padding: 0 15px;
  min-width: 50px;
  text-align: center;
  background: rgba(255,255,255,0);
  text-decoration: none;
  color: #FFF;
  will-change: background-color;
}

.header nav a:hover,
.header nav a:active {
  background: rgba(0,0,0,0.2);
}

.header nav a.active {
  background: rgba(0,0,0,0.4);
}

.home {
  padding: 56px 20px;
  min-height: 100%;
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.home h1 {
  color: #333;
}

.home .card_container {
  width: 100%;
  padding: 10px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.home .card {
  width: 100%;
  max-width: 320px;
  margin: 10px;
  padding: 5px;
  background-color: #fff;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
}

.home .card .img {
  width: 100%;
  height: 150px;
  /*min-width: 120px;*/
  background-color: #999;
}

.home .card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.home .card .title {
  color: #333;
  margin: 5px;
}

.home .card .body {
  color: #333;
  margin: 5px 5px 8px 5px;
}

.home .card .empty {
}



